<template>
  <div class="d-flex">

    <div class="db-inner__template-wrapper">
      <div class="db-inner__template-header d-flex justify-content-between mb-3">
        <div class="d-flex" style=" position:relative;">
          <button
            @click="addProductModal=true"
            style="color: #3476a4;"
          > 
            <i class="fas fa-plus mr-2"></i>
            Додати товар
          </button>

          <div style="position:relative;">
            <button
              @click="groupAct=!groupAct"
              style="font-size:14px;font-weight:200;"
              class="dropdown__arrow"
              data-popup='true'
            >
              Груповi дiї
            </button>
            <PopUp
              @close="groupAct=false"
              :visible="groupAct"
            >
              <ul>
                <li>
                  <button
                    class="text-center"
                  >
                    Редактировать
                  </button>
                </li>
                <li>
                  <button
                    style="white-space: nowrap;"
                    class="text-center"
                  >
                    Изменить категорию
                  </button>
                </li>
                <li>
                  <button
                    class="text-center"
                  >
                    Дублировать
                  </button>
                </li>
                <li>
                  <button
                    class="text-center"
                  >
                    Remove
                  </button>
                </li>
              </ul>
            </PopUp>
          </div>

          <div style="position:relative;">
            <button
              @click="mobileCat=!mobileCat"
              class="dropdown__arrow d-block d-lg-none"
              data-popup='true'
            >
              Категорії 
            </button>
            <PopUp
              class="d-block d-md-none"
              @close="mobileCat=false"
              :visible="mobileCat"
            >
              <button
                @click="filterCategory(cat)"
                v-for="cat in categories"
                :key="`mobile_${cat}`"
                class="text-center"
              >
                {{cat}}
              </button>
            </PopUp>
          </div>

        </div>
        <p> 
          count: {{data.length}}
        </p>
      </div>
      <div class="db-inner__template-body">
        <ul class="d-flex flex-column">
          <li 
            style="border-bottom: 1px solid rgba(0, 0, 0, 0.125);background: #4296d0;color:#fff;"
            class="d-flex justify-content-between p-2"
          >
            <FineCheck
              class="my-auto"
              :name="`dash_price_main`"
              width="16%"
            />
            <p>Имя</p>
            <p>Категория</p>
            <p>Цена</p>
            <p>Статус</p>
            <p></p>
          </li>
        </ul>
        <ul class="d-flex flex-column">
          <li 
            v-for="(item) in dataReceive"
            :key="item.id"
            style="border-bottom: 1px solid rgba(0, 0, 0, 0.125)"
            class="d-flex justify-content-between p-2"
          >
            <FineCheck
              class="my-auto"
              :name="`dash_price_${item.id}`"
              width="16%"
            />
            <p>{{item.name}}</p>
            <p>{{item.category_name}}</p>
            <p>{{item.price}} {{item.currency}}</p>
            <p></p>
            <button class="dropdown__arrow">Дiї</button>
          </li>
        </ul>
      </div>
    </div>

    <div class="db-inner__template-filter d-none d-lg-block mb-3">
      <h2 class="filter__title px-2 pb-3"> 
         Категорії: {{categories.length -1 }} 
      </h2>
      <div class="d-flex flex-column mt-3">
        <button
          @click="filterCategory(cat)"
          :class="{'active': curCat === cat}"
          class="filter__btn text-left px-3 py-2"
          v-for="cat in categories"
          :key="cat"
        >
          {{cat}}
        </button>
      </div>
    </div>

    <transition name="modal">
      <Modal
        v-if="addProductModal"
        type="full"
        class="modal__addProduct"
      >
        <div class="modal__addProduct-header d-flex justify-content-between align-items-center py-4 px-5">
          <h2 class="modal__addProduct-header-title">Додати товар</h2>
          <button
            @click="addProductModal=false"
            style="background:transparent;"
            class="p-0"
            type="button"
          >
            <span class="modal__addProduct-header-btn">×</span>
          </button>
        </div>
        <div class="modal__addProduct-body d-flex flex-column justify-content-between align-items-center p-5">
          <div class="d-flex mb-3">
            <img
              class="mx-3"
              width="350px"
              src="https://gatovimdoma.ru/wp-content/uploads/2016/01/picca-kaprichoza.jpg"
              alt=""
            >

            <div
              style="background-color: #dee2e6;" 
              class="w-100 d-flex align-items-center justify-content-center mx-3"
            >
              <div class="text-center">
                <form
                  action=""
                  method="post"
                  enctype="multipart/form-data"
                  class="dropzone"
                  id="productImage"
                >
                  <div class="fallback tx-center">
                    <input type="file" name="photoFile" id="photoFile">
                  </div>
                </form>
              </div>
            </div>
          </div>

          <div class="row w-100">
            <div class="col-lg-9 mb-3">
              <input type="text" class="form-control p-4" id="addProductModalName" placeholder="Назва товару">
            </div>
            <div class="col-lg-3 mb-3">
              <input type="number" step="0.01" class="form-control p-4" id="addProductModalPrice" placeholder="Цена">
            </div>
            <input type="text" class="form-control p-4 mb-3 mx-3" id="addProductModalShortAbout" placeholder="Описание">
            <input type="text" class="form-control p-4 mb-3 mx-3" id="addProductModalFullAbout" placeholder="Детали товара">
          </div>

        </div>

        <div class="modal__addProduct-footer p-3">
            <button
              @click="addProductModal=false"
              style="background-color: #6c757d; border-radius: 0;"
              type="button"
              class="btn text-white"
            >
              Cкасувати
            </button>
            <button
              @click="addProductModal=false"
              style="background: #4296d0; border-radius: 0;"
              type="button"
              class="btn text-white mr-4 ml-2"
            >
              Зберегти
            </button>
        </div>

      </Modal>
    </transition>

  </div>
</template>

<script>

export default {
  data() {
    return {
      dataReceive: this.data,
      curCat: 'Все',
      categories: [],
      mobileCat: false,
      groupAct: false,
      addProductModal: false
    }
  },
  components: {
    FineCheck: () => import('@/components/general/checkbox'),
    PopUp: () => import('@/components/popUp'),
    Modal: () => import('@/components/layout/modal'),
    // Modal: () => import('@/components/layout/modal'),
  },
  mounted() {
    const cat = ['Все']
    this.dataReceive.forEach((item) => {
      if(!cat.includes(item['category_name'])) {
        cat.push(item['category_name'])
      }
    })
    this.categories = cat
    console.log(this.data);
  },
  methods: {
    filterCategory(category) {
      let filteredData = []

      if(category === 'Все') {
        filteredData = this.data
      }
      else {
        this.data.forEach((item) => {
          if(item['category_name'] === category) filteredData.push(item)
        })
      }

      this.curCat = category
      this.dataReceive = filteredData
    }
  },
  props: ['data']
}
</script>

<style lang="scss" scoped>
  .db-inner__template {

    &-wrapper {
      width:100%;

      @media (max-width: 767px) {
        div & {
          width:100%;
        }
      }
    }

    &-header {
      border: 1px solid rgba(0, 0, 0, 0.125);
      padding: 15px;
    }

    &-body {

      & ul:nth-child(1) p {
        font-size: 12px;
        letter-spacing: 0.5px;
        font-weight: 300;
        text-transform: uppercase;
      }

      & ul:nth-child(2) {
        border: 1px solid rgba(0, 0, 0, 0.125);
        border-top: none;
      }

      p,button {
        width: 16%;
        text-align: center;
        padding: 5px;
      }
    }

    button {
      cursor: pointer;
      background: #fff;
    }

    &-filter {
      width: 30%;
      margin-left: 30px;
      border: 1px solid rgba(0, 0, 0, 0.125);
      height: 100%;
      padding: 15px;
    }
  }

  .filter__title {
    text-transform: uppercase;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
    font-weight: 600;
    font-size: 13px;
    color: #343a40;
  }

  button.filter__btn {

    &:focus {
      outline: none;
    }

    &.active {
      background: rgb(66, 150, 208) none repeat scroll 0% 0%;
      color: #fff;
    }
  }

  .modal__addProduct {
    height: 100%;

    &-header {
      background: #4296d0;

      &-title {
        color: #fff;
        font-size: 1.25rem;
        font-weight: 200;
      }

      div &-btn {
        font-size: 32px;
        font-weight: 800;
        color:#fff;
        background: transparent;
      }
    }

    &-footer {
      position: fixed;
      bottom: 0;
      right: 0;
    }
  }

</style>